:root {
    --space: 1.5em 0;
    --space: 2em 0;
}

.Site {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.Site-content {
    flex: 1 0 auto;
    padding: var(--space) var(--space) 0;
    width: 100%;
}

.Site-content:after {
    content: '\00a0';
    display: block;
    margin-top: var(--space);
    height: 0;
    visibility: hidden;
}

.ui.input {
 /* margin-left: -6.66vw;*/
  /*  margin-top: 1vh;*/
    font-size: 1em;
    width: 50vw;
}

.ui.inverted.menu {
    border: 0 solid transparent;
    background: #6e925e;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.container {
  position:relative
}


.text-field {
  float: left;
  margin-top: 3vh;
  vertical-align: middle;
  text-align: center;
}

.nav-bar {
  background-color: #333;
}
.padding {
  padding-left: 2vw;
}
.icon{
  height:6vh;
  width: 4vw;
}

.nav-item {
  float: right;
  display: inline-block;
  color: white;
  text-align: center;
  text-decoration: none;
    margin-top: 1vh;
  padding: 14px 16px;
}

.nav-item:hover {
  background-color: #111;
}
.title {
  width: 100vw;
  height: 100%;
  text-align: center;
  background-color: #333;
  font-size: 5vw;
  position: absolute;
  opacity: 0.5;
}
.view {
  height: 100vh;
  width: 100vw;
}


.dropdown-content {
  color: white;
  right: 0;
  display: none;
  position: absolute;
  z-index: 1000 !important;
  background-color: #111;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  text-overflow: 10%;
}

.nav-item:hover .dropdown-content {display: block;}

.grid-container {
  height:100vh; 
  display: grid;
  grid-template-columns: auto auto auto;
  background-color: #4c525b;
  padding: 2px;
}
.grid-item {
  top:50%;
  height: 250px;
  width: 200px;
  background-color: rgba(255, 255, 255, 0.3);
  border: 0.2px solid rgba(0, 0, 0, 0.8);
  padding: 2px 2px;
  font-size: 15px;
  text-align: center;
  margin-bottom: 10px;
  margin-top: 10px;
  margin-left: 10px;
}